@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("PT Mono-regular"), url("../fonts/pt-mono-v8-latin_cyrillic-ext-regular.woff2") format("woff2"), url("../fonts/pt-mono-v8-latin_cyrillic-ext-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed-regular"), url("../fonts/roboto-condensed-v19-latin_cyrillic-ext-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-latin_cyrillic-ext-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed-bold"), url("../fonts/roboto-condensed-v19-latin_cyrillic-ext-700.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-latin_cyrillic-ext-700.woff") format("woff"); }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.page {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow-y: scroll;
  scroll-behavior: smooth;
  height: 100%; }

.page-body {
  min-height: 100%;
  background-color: #ffffff;
  color: #000000;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 21px; }

.page-body h1 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase; }

.button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  padding: 14px 13px 14px 12px;
  border: 1px solid #ffffff;
  border-radius: 5px;
  background-color: transparent; }

.page-header {
  background-color: #1b2d37; }

.page-header__contacts {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 1411px;
  margin: 0 auto;
  padding-top: 33px; }
  @media (max-width: 1023px) {
    .page-header__contacts {
      width: 720px; } }
  @media (max-width: 767px) {
    .page-header__contacts {
      width: 288px; } }

@media (max-width: 767px) {
  .page-header__logo-image {
    width: 38px;
    height: 38px; } }

.contacts-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 659px;
  padding: 0;
  margin: 0;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }
  @media (max-width: 1023px) {
    .contacts-header {
      width: 72px; } }

.contacts-header__link {
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
  text-decoration: none; }
  @media (max-width: 1023px) {
    .contacts-header__link {
